<template>
  <div class="layout">
    <a-layout class="layout-body">
      <a-layout-sider
        class="layout-sider"
        breakpoint="md"
        v-model="collapsed"
        @collapse="onCollapse"
      >
        <div class="logo">
          <img src="../common/img/logo.png" alt="" />
        </div>
        <SiderMenu />
      </a-layout-sider>
      <a-layout :style="{ marginLeft: layoutMainMarginLeft }">
        <Header :paddingLeft="layoutMainMarginLeft" :collapsed="collapsed" />
        <a-layout-content class="layout-content">
          <div class="layout-content-main">
            <router-view />
          </div>
        </a-layout-content>
        <Footer />
      </a-layout>
    </a-layout>
  </div>
</template>
<script>
import SiderMenu from './SiderMenu';
import Header from './Header';
import Footer from './Footer';
export default {
  data() {
    return {
      collapsed: false,
      layoutMainMarginLeft: '200px',
    };
  },
  mounted() {},
  methods: {
    onCollapse() {
      if (this.collapsed) this.layoutMainMarginLeft = '80px';
      else this.layoutMainMarginLeft = '200px';
    },
  },
  components: {
    SiderMenu,
    Header,
    Footer,
  },
};
</script>

<style lang="less">
.layout-body {
  min-height: 100vh;
  .layout-sider {
    overflow: auto;
    height: 100vh;
    position: fixed;
    left: 0;
    text-align: left;
  }
  .layout-content {
    margin: 24px 16px 0;
    overflow: initial;
    margin-top: 80px;
    .layout-content-main {
      padding: 24px;
      min-height: calc(100vh - 150px);
    }
  }
  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    background: rgba(255, 255, 255, 0.2);
    margin: 8px 16px;
    img {
      max-height: 100%;
      max-width: 100%;
    }
  }
}
</style>
