<template>
  <a-layout-header
    :style="{ width: 'calc(100% - ' + paddingLeft + ')' }"
    class="header-main"
  >
    <div class="user-dropdown">
      <a-badge :dot="messageCount > 0">
        <a-icon type="message" />
      </a-badge>
      <a-dropdown v-model="visible">
        <a class="ant-dropdown-link" @click="e => e.preventDefault()">
          <a-icon type="user" />
        </a>
        <a-menu slot="overlay" @click="handleMenuClick">
          <a-menu-item key="1"> 欢迎: {{ username }} </a-menu-item>
          <a-menu-divider />
          <a-menu-item key="2">
            <span class="text">信息完善</span>
          </a-menu-item>
          <a-menu-item key="3">
            <span class="text">退出登录</span>
          </a-menu-item>
        </a-menu>
      </a-dropdown>
    </div>
  </a-layout-header>
</template>

<script>
export default {
  props: {
    paddingLeft: {
      type: String,
      default: '200px',
    },
  },
  data() {
    return {
      username: 'Darren',
      current: ['mail'],
      visible: false,
      messageCount: 6,
    };
  },
  methods: {
    handleMenuClick(e) {
      if (e.key === '3') {
        this.visible = false;
      }
    },
  },
};
</script>

<style lang="less">
.header-main {
  background: #fff;
  padding: 0;
  position: fixed;
  z-index: 1;
  width: 100%;
  .user-dropdown {
    position: absolute;
    right: 15px;
    top: 0;
    .message {
      font-size: 24px;
      color: @crm-theme-bg-color;
      cursor: pointer;
    }
    .ant-dropdown-link {
      margin-left: 10px;
      color: @crm-theme-bg-color;
    }
  }
}
</style>
